@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{margin:0;padding:0;width:100vw;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif}#root{width:100vw;height:100vh}.mapboxgl-popup-content{background:#364f6b !important}.mapboxgl-popup-tip{border-bottom-color:#364f6b !important}.mapboxgl-popup-close-button{color:#fc5185;font-size:2rem;outline:none}

.entryForm{margin-right:0.5rem;height:35rem;display:flex;flex-direction:column;align-items:center;justify-content:center;font-family:"Roboto",sans-serif;color:white}.entryForm label{font-size:1.2rem;margin:0.5rem}.entryForm input,.entryForm textarea{width:100%;height:2rem;background-color:transparent;border:1px solid rgba(54,179,185,0.741176);outline:none;text-align:center;font-family:"Roboto",sans-serif;color:white;text-align:center;margin:5px}.entryForm ::-webkit-calendar-picker-indicator{-webkit-transition:0.5s ease-in-out all;transition:0.5s ease-in-out all;-webkit-filter:invert(1);filter:invert(1);outline:none;cursor:pointer}.entryForm ::-webkit-calendar-picker-indicator:hover{transform:scale(1.2)}.entryForm__button{height:3rem;width:10rem;margin-top:1rem;border:3px solid transparent;outline:none;background-color:#fc5185;padding:1rem 2rem 1rem 2rem;font-weight:700;border-radius:999px;transition:0.3s ease-in-out all;font-family:"Roboto",sans-serif}.entryForm__button:hover{cursor:pointer;border:3px solid white}

.sidebar{width:35rem;background-color:#364f6b;color:#f0f0f0;font-family:"Roboto",sans-serif;display:flex;flex-direction:column;align-items:center;justify-content:top}.sidebar h3{font-size:1.8rem}.sidebar ul{margin:0;padding:0;margin-block:0;width:100%}.sidebar ul li{height:4rem;width:100%;display:flex;align-items:center;text-decoration:none;list-style:none;cursor:pointer;border-radius:1rem;transition:1s ease-in all}.sidebar ul li:hover{background-color:rgba(54,179,185,0.741176);color:black}.sidebar ul li h4{margin-left:4rem;width:100%}.sidebar__item{display:flex;justify-content:center;align-items:center;position:relative}.sidebar__item button{background-color:#fc5185;border-radius:9px;padding:0.5rem;border:none;outline:none;position:absolute;cursor:pointer;transition:1s ease-in-out all;right:2rem}.sidebar__item button:hover{transform:scale(1.2)}

.popup{min-width:20rem;max-width:40rem;padding:1rem;background-color:#364f6b;display:flex;flex-direction:column;align-items:centers;justify-content:center;text-align:center;color:white;font-family:"Roboto",sans-serif}.popup h3{font-size:1.5rem;margin:0}.popup p{font-size:1.2rem}.popup button{margin-top:1rem;background-color:#fc5185;border-radius:999px;align-self:center;width:12rem;height:3.5rem;font-family:"Roboto",sans-serif;font-weight:700;font-size:1rem;border:3px solid transparent;outline:none;transition:1s ease-in-out all}.popup button:hover{border:3px solid white;cursor:pointer}.popup__complete{display:flex;flex-direction:column}.popup img{padding-top:1rem;padding-bottom:2rem;object-fit:cover;width:20rem;height:20rem;text-align:center;margin:0 auto;display:block}

.marker{transform:translate(-50%, -100%)}.marker.yellow{fill:yellow}.app{display:flex;width:100vw;height:100vh}

